nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    background-color: var(--black);
    box-shadow: 0px 5px 120px var(--black);
    -webkit-tap-highlight-color: transparent;
}
.logo{
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 5px;
    color: var(--purple);
    cursor: pointer;
}
.cover{
    visibility: hidden;
    position: absolute;
    width: 100%;
    height: calc(100vh - 80px);
    top: 80px;
    opacity: 0;
    background-color: var(--black);
    transition: opacity .13s ease-out, visibility .13s;
}
.nav-links{
    z-index: 1;
    display: flex;
    align-items: center;
    width: 50%;
    height: 100%;
}
.social-links{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 80%;
}
.nav-links > li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    list-style: none;
    width: 100%;
    height: 100%;
}
.nav-links li .border{
    border-bottom: 2px solid var(--blue);
    width: 0;
    height: 0;
    transition: width .13s ease-out;
}
.nav-links li button{
    cursor: pointer;
    background: transparent;
    outline: none;
    border: 0;
}
.nav-links li:last-child{
    background-color: var(--dark-grey);
}
.nav-links > li > button{
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 3px;
    font-size: 14px;
    color: var(--white);
    font-weight: 500;
    height: 100%;
    width: 100%;
}
.nav-links li button div{
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.nav-links li button i{
    color: var(--purple);
    font-size: 19px;
}
.more{
    border: 0;
    display: none;
    color: var(--white);
    cursor: pointer;
}
@media (hover: hover){
    .nav-links button *{
        transition: transform .35s ease, 
                    color .35s ease,
                    background-color .3s ease;
    }
    .nav-links > li > button:hover{
        background-color: var(--grey);
    }
    .nav-links button:hover ~ .border{
        width: 100%;
    }
    .nav-links button:hover p{
        transform: scale(1.15);
    }
    .nav-links button:hover i{
        transform: rotateY(360deg);
        color: var(--blue);
    }
}
@media (hover: none){
    .nav-links button *{
        transition: transform .35s ease, 
                    color .35s ease,
                    background-color .3s ease;
    }
    .nav-links > li > button:active{
        background-color: var(--grey);
    }
    .nav-links button:active ~ .border{
        width: 100%;
    }
    .nav-links button:active p{
        transform: scale(.85);
    }
    .nav-links button:active i{
        transform: rotateY(360deg);
        color: var(--blue);
    }
}
@media screen and (max-width:1024px){
    .nav-links{
        width: 60%;
    }
    .social-links{
        width: 100%;
    }
}
@media screen and (max-width:768px){
    body{
        overflow-x: hidden;
    }
    nav{
        justify-content: center;
    }
    .nav-links{
        position: absolute;
        right: 0px;
        top: 80px;
        height: calc(100vh - 80px);
        width: 50%;
        background-color: var(--black);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        transform: translateX(100%);
        transition: transform .13s ease-out;
    }
    .social-links{
        width: 70%;
    }
    .nav-links li{
        height: 100%;
        width: 100% !important;
    }
    .nav-links li button div{
        flex-direction: row;
        align-items: center;
        width: 45%;
    }
    .nav-links i{
        font-size: 22px;
    }
    .nav-links button{
        flex-direction: row !important;
        justify-content: space-around !important;
        width: 100%;
        height: 100%;
        font-size: 16px;
    }
    .more i{
        transition: transform .13s ease-out;
    }
    .nav-active{
        transform: translateX(0%);
    }
    .nav-active ~ .more i{
        transform: rotateZ(180deg);
    }
    .nav-active ~ .cover{
        opacity: .8;
        visibility: visible;
    }
    .more{
        display: block;
        position: absolute;
        right: 10%;
    }
}
@media screen and (max-width:610px){
    .nav-links li button div{
        width: 55%;
    }
}